* {
  font-family: Roboto;
  margin-bottom: -1px;
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');
:root {
  background-color: #2f2fa2;
  --text: rgb(211, 211, 211);
  --font-size-m: 50%;
  --font-family-roboto: "Roboto";
}


*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.509) !important;
  background-color: #2f2fa2 !important;
}

*::-webkit-scrollbar {
  height: 0px !important;
  width: 10px !important;
  background-color: #f5f5f5 !important;
}

*::-webkit-scrollbar-thumb {
  border-radius: 10px !important;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3) !important;
  background-color: #bfbfbf !important;
}

body {
  overflow-x: hidden !important;
}

/* ----NavBar----------------------------------------------------------------------------- */

.nav {
  display: flex;
  flex-direction: column;
  background-color: #2f2fa2;
  position: fixed;
  column-gap: 10px;
  bottom: 10px;
  left: 20px;
}

/* ----landing page---------------------------------------------------------------------------------*/

.landing{
  background-color: #2f2fa2;
  display: flex;
  justify-content: center;
}

.hero{
  margin-top: 34px;
  width: 97vw;
  height: auto;
  padding-bottom: 5%;
  margin-left: auto;
  margin-right: auto;
}

@keyframes bounce {
  0% { transform: translateY(0); }
  100% { transform: translateY(-10); }
}
.bounce {
  position: absolute;
  left: 49%;
  top: 94%;
  animation: bounce 1.5s;
  /* animation-direction: alternate; */
  animation-iteration-count: infinite;
}

/* ----About me----------------------------------------------------------------------------------------*/

.about-section {
  background-color: #2f2fa2;
  display: flex;
  width: 100vw;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 9vh;
  border-bottom: 0px;
}

.about {
  display: flex;
  position: relative;
  align-items: center;
  flex-direction: column;
  width: 50vw;
  /* height: 65vh; */
  background: black;
  padding-bottom: 30px;
  padding-top: 30px;
  margin-left: 14%;
  border-radius: 69px;
  /* transform: scale(0.7); */
}

.about-title {
  margin-right: 20px;
  width: 230px;
  height: 89vh;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 5px;
  -webkit-text-stroke-color: #f64c72;
  font-weight: 600;
  z-index: 0;
}

#myPic {
  max-width: 220px;
  align-self: center;
  padding: 20px;
  margin: auto;
}

#about-me {
  display: flex;
  justify-content: flex-start;
  max-width: 80%;
  max-height: 60%;
  text-align: left;
  line-height: 22px;
  color: rgb(211, 211, 211);
  flex-wrap: wrap;
  font-size: 18px;
}

#grabber {
  font-weight: 400;
  text-align: left;
  color: rgb(211, 211, 211);
  font-size: 2rem;
  width: 60%;
  align-self: center;
  text-align: center;
  padding: 30px 20px 20px 30px;
  /* min-width: 90%; */
}

.highlight {
  color: #f64c72;
}

.about-divider {
  width: 65%;
  border-bottom: 0.25rem solid slategrey;
  z-index: 1;
  border-radius: 20px;
  opacity: 0.5;
}

/* -------------------Skills-------------------------------------------------------------- */
.skills-section {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  background-color: #2f2fa2;
  height: 95vh;
}

.skills-title{
  margin-right: 20px;
  width: 230px;
}

.skills-container {
  padding-top: 30px;
  width: 50vw;
  height: min-content;
  align-self: center;
  margin-left: 14%;
  background-color: #2f2fa2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 7%;
}

.html5,
.css3,
.js,
.react,
.git,
.sass,
.bootstrap,
.figma,
.node {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.html5,
.css3,
.js,
.react,
.git,
.sass,
.bootstrap,
.figma,
.node {
  color: black;
}

.html5 {
  color: rgb(205 51 28);
}
.css3 {
  color: rgb(20 87 157);
}
.js {
  color: rgb(221 200 59);
}
.bootstrap {
  color: rgb(79 41 123);
}
.git {
  color: rgb(0 0 0);
}
.react {
  color: rgb(78 196 234);
}
.sass {
  color: rgb(179 75 127);
}

.figma {
  color: rgb(149, 0, 0);
}

.node {
  color:rgb(50, 140, 42)
}

/* ----------------Iframe----------------------------------------------------------------- */

.iframe-wrapper {
  position: relative;
  margin-top: -20px;
  padding-top: 26%;
  width: 90%;
  margin-bottom: 4%;
}

.iframe-wrapper iframe {
  position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border-radius: 30px;
}



/* --------------- Projects-------------------------------------------------------------- */

.projects {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  background-color: #2f2fa2;
}

.projects-title {
  align-self: right;
  font-weight: 700;
  color: rgb(211, 211, 211);
  background: #2f2fa2;
  margin-right: 20px;
  width: 300px;
  height: 70vw;
}

.projects-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 70%;
  margin-left: 5%;
  gap: 50px;
  flex-wrap: wrap;
  background: #2f2fa2;
  height: 100%;
}

.site-btn {
  text-decoration: none;
  color: black;
  background-color: white;
  padding: 8px;
  margin: auto;
  float: left;
  width: 10vw;
  text-align: center;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #f64c72;
  border-radius: 10px;
  transition: 0.1s ease-in-out;
}

.site-btn:hover {
  color: white;
  box-shadow: 0 1px 18px rgba(255, 0, 130, 0.5);

}

.site-btn:active {
  transform: scale(0.9);
  box-shadow: 0 1px 9px rgba(255, 0, 130, 0.5);
}

a a:hover,
.button {
  color: white;
  text-decoration: none;
}

.btn-container:hover .button {
  opacity: 1;
}

.button:hover {
  background: #f64c72;
}

.mobile {
  display: none;
}

/* -----------------------------------------CONTACT PAGE-------------------------------------------------------------- */

.color {
  /* padding-top: 20%; */
  display: flex;
  justify-content: center;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;

  height: 100vh;
  overflow: hidden;
  background-color: #2f2fa2;
}

.contact-desc{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
}

.contact-desc h3 {
  text-align: center;
  color: white;
  font-weight: 100;
}

#email{
  padding-top: 20px;
  margin-bottom: 0;
  color: white;
  font-weight: 300;
  transition: all .2s ease-in-out;
}

.contact-desc a {
  text-align: center;
  color: #f64c72;
  margin-bottom: 0;
  text-decoration: none;
}

.contact-desc a:hover {
  color: #f64c72;
}

.follow{
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10px;
  transition: all .2s ease-in-out;
}

.follow h1:hover{
  transform: scale(1.1);
}

.contact-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 30em;
  height: min-content;
  min-width: 228px;
  background: black;
  border-bottom-right-radius: 60px;
  border-top-left-radius: 60px;
  padding: 20px;
  padding-top: 55px;
}

h1 {
  text-align: center;
  padding-bottom: 15px;
  color: white;
}

.contactFormTitle {
  color: #e9e9e9;
  font-weight: 700;
  text-transform: uppercase;
}

/* .MuiTextField-root {
  color: white !important;
  width: 85%;
  padding-bottom: 30px;
  margin-bottom: 20px;
} */

.MuiInputLabel-root {
  color: red;
}

#contact-text label{
  color: white;
  background: white;

}

.MuiButton-root {
  width: 27ch;
  padding: 20px;
}

/* -------------FOOTER---------------- */

.copyright {
  text-align: center;
  color: rgb(211, 211, 211);
  padding-top: 30px;
  font-family: var(--font-family-roboto);
  background-color: #2f2fa2;
  font-weight: 100;
}

/* --------------------Media Queries--------------------------------- */

@media only screen and (max-width: 768px) {
  a {
    padding: 10px;
  }

  .bounce{
    display: none;
  }

  a .hover {
    /* padding-left: 10px; */
    /* padding-right: 10px !important; */
    width: 110px;
  }

  .about-section {
    padding-bottom: 10%;
  }

  .recent-bio {
    padding-left: 5%;
    padding-right: 5%;
  }

  .about-section {
    flex-direction: column;
  }

  .about-section .about-title{
    font-size: 29vw;
    position: inherit;
    margin-bottom: -4vh;
    top: 25px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #f64c72;
  }

  .about {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    justify-content: center;
    border-top-left-radius: 60px;
    border-bottom-right-radius: 60px;
    padding: 5px;
  }
  
  #myPic {
    /* margin: 30px;
    border-radius: 50em; */
  }

  #grabber {
    width: 90%;
    text-align: center;
    margin-top: 19px;
  }

  .skills-section{
    height: 60vh;
  }

  .skills-title{
    font-size: 29vw;
    margin-bottom: -4vh;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #f64c72;
  }

  .recent-title{
    font-size: 29vw;
    position: inherit;
    /* transform: rotate(90deg);  */
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #f64c72;
  }

  

  #submit {
    width: 40%;
    padding: auto;
    height: min-content;
  }
}

@media only screen and (max-width: 614px) {
  .hero {
    padding-bottom: 0;
  }

  .about-section {
    padding-top: 0;
  }

  .title {
    width: 100%;
    font-size: 8vw;
    opacity: 1;
  }

  .about-title {
    transform: rotate(-90deg);
    height: 40vh;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: -23% !important;
  }

  .about {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    border-top-left-radius: 60px;
    border-bottom-right-radius: 60px;
    padding: 5px;
  }

  .about #grabber {
    position: inherit;
    font-size: 5vw;
    margin-bottom: 0;
  }
  
  .about #myPic {
    position: inherit;
    margin: 0;
    padding: 50px;
    min-width: 40%;
    min-height: auto;
    
  }

  .skills-section{
    height: min-content;
    flex-direction: column;
  }

  .skills-title{
    /* margin-bottom: -4vh; */
    margin-left: 40%;
    height: 30%;
    width: 20%;
    transform: rotate(-90deg);
    margin-bottom: -15% !important;
  }

  .skills-container {
    padding-top: 0;
    margin-left: 0 !important;
  }

  .projects{
    flex-direction: row-reverse;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 60px;
    padding-top: 40px;
    padding-left: 10px;
  }

  .projects-title{
    margin: auto;
    width: 30%;
    height: 80%;
    padding: 2px;
  }

  .projects-container img {
    width: 100%;
    border: 3px solid black;
    border-radius: 20px;
  }

  .projects-container {
    /* padding-bottom: 30%; */
    flex-direction: column;
    justify-content: space-between;
    width: 60%;
    gap: 0;
    margin-left: 0;
  }

  article {
    display: none !important;
  }

  .mobile {
    display: block;
    width: 100%;
    margin-right: 10px;
  }

  .contact-container {
    width: 80%;
    height: 50%;
    gap: 2vh;
  }

  .color {
    height: 90%;
  }


  .row input {
    width: 100%;
  }

  #submit {
    width: 100%;
    padding: auto;
    height: min-content;
    background-color: #f64c72;
  }

  .wrapper {
    display: none;
  }
}
